.filters-section {
  margin-bottom: 2rem;
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.filters-section .filters-container {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
  position: relative;
}
.filters-section .filters-container .search-input {
  flex: 1;
  min-width: 250px;
  position: relative;
}
.filters-section .filters-container .search-input .search-field {
  width: 100%;
  padding: 0.75rem 1rem 0.75rem 2.5rem;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  font-size: 0.875rem;
  background: #f9fafb;
  transition: all 0.2s ease;
}
.filters-section .filters-container .search-input .search-field:focus {
  outline: none;
  border-color: #2563eb;
  background: white;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}
.filters-section .filters-container .search-input .search-field::placeholder {
  color: #9ca3af;
}
.filters-section .filters-container .search-input .search-icon {
  position: absolute;
  left: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  color: #9ca3af;
  font-size: 0.875rem;
}
.filters-section .filters-container .filter-dropdowns {
  display: flex;
  gap: 1rem;
  align-items: center;
}
.filters-section .filters-container .filter-dropdowns .filter-dropdown {
  position: relative;
}
.filters-section .filters-container .filter-dropdowns .filter-dropdown .filter-button {
  padding: 0.75rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  background: white;
  color: #374151;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
  min-width: 140px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5rem;
}
.filters-section .filters-container .filter-dropdowns .filter-dropdown .filter-button:hover {
  border-color: #9ca3af;
  background: #f9fafb;
}
.filters-section .filters-container .filter-dropdowns .filter-dropdown .filter-button:focus {
  outline: none;
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}
.filters-section .filters-container .filter-dropdowns .filter-dropdown .dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  z-index: 50;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-8px);
  transition: all 0.2s ease;
  margin-top: 0.25rem;
}
.filters-section .filters-container .filter-dropdowns .filter-dropdown .dropdown-menu.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.filters-section .filters-container .filter-dropdowns .filter-dropdown .dropdown-menu .dropdown-item {
  padding: 0.75rem 1rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
  border-bottom: 1px solid #f3f4f6;
}
.filters-section .filters-container .filter-dropdowns .filter-dropdown .dropdown-menu .dropdown-item:last-child {
  border-bottom: none;
}
.filters-section .filters-container .filter-dropdowns .filter-dropdown .dropdown-menu .dropdown-item:hover {
  background-color: #f9fafb;
}
.filters-section .filters-container .filter-dropdowns .filter-dropdown .dropdown-menu .dropdown-item:first-child {
  border-radius: 8px 8px 0 0;
}
.filters-section .filters-container .filter-dropdowns .filter-dropdown .dropdown-menu .dropdown-item:last-child {
  border-radius: 0 0 8px 8px;
}
.filters-section .filters-container .advanced-button {
  padding: 0.75rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  background: white;
  color: #374151;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  white-space: nowrap;
}
.filters-section .filters-container .advanced-button:hover {
  border-color: #9ca3af;
  background: #f9fafb;
}
.filters-section .filters-container .advanced-button:focus {
  outline: none;
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}
.filters-section .filters-container .advanced-button.active {
  background: #2563eb;
  color: white;
  border-color: #2563eb;
}
.filters-section .filters-container .advanced-button.active:hover {
  background: #1d4ed8;
}
.filters-section .filters-container .clear-button {
  padding: 0.75rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  background: white;
  color: #6b7280;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  white-space: nowrap;
}
.filters-section .filters-container .clear-button:hover {
  border-color: #ef4444;
  color: #ef4444;
  background: #fef2f2;
}
.filters-section .filters-container .clear-button:focus {
  outline: none;
  border-color: #ef4444;
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
}
.filters-section .filters-container .advanced-options {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: white;
  border: 1px solid #d1d5db;
  border-radius: 12px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  z-index: 40;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-8px);
  transition: all 0.2s ease;
  margin-top: 0.5rem;
  padding: 1.5rem;
}
.filters-section .filters-container .advanced-options.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.filters-section .filters-container .advanced-options .advanced-section {
  margin-bottom: 1.5rem;
}
.filters-section .filters-container .advanced-options .advanced-section:last-child {
  margin-bottom: 0;
}
.filters-section .filters-container .advanced-options .advanced-section h4 {
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 0.75rem 0;
}
.filters-section .filters-container .advanced-options .advanced-section .sort-options {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}
.filters-section .filters-container .advanced-options .advanced-section .sort-options .sort-option {
  padding: 0.5rem 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  background: white;
  color: #374151;
  font-size: 0.75rem;
  cursor: pointer;
  transition: all 0.2s ease;
}
.filters-section .filters-container .advanced-options .advanced-section .sort-options .sort-option:hover {
  border-color: #9ca3af;
  background: #f9fafb;
}
.filters-section .filters-container .advanced-options .advanced-section .sort-options .sort-option.active {
  background: #2563eb;
  color: white;
  border-color: #2563eb;
}
.filters-section .filters-container .advanced-options .advanced-section .sort-order-button {
  padding: 0.5rem 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  background: white;
  color: #374151;
  font-size: 0.75rem;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.filters-section .filters-container .advanced-options .advanced-section .sort-order-button:hover {
  border-color: #9ca3af;
  background: #f9fafb;
}

@media (max-width: 768px) {
  .filters-section {
    padding: 1rem;
  }
  .filters-section .filters-container {
    flex-direction: column;
    align-items: stretch;
    gap: 1rem;
  }
  .filters-section .filters-container .search-input {
    min-width: auto;
  }
  .filters-section .filters-container .filter-dropdowns {
    justify-content: space-between;
    gap: 0.5rem;
  }
  .filters-section .filters-container .filter-dropdowns .filter-dropdown .filter-button {
    min-width: 120px;
  }
  .filters-section .filters-container .advanced-button,
  .filters-section .filters-container .clear-button {
    align-self: stretch;
    justify-content: center;
  }
  .filters-section .filters-container .advanced-options {
    position: static;
    margin-top: 1rem;
    transform: none;
  }
}
